:root {
  --transition: all 0.3s ease 0s;
}

a,
button,
input {
  -webkit-transition: var(--transition);
  transition: var(--transition);
  text-decoration: none !important;
}

.noUl {
  list-style: none;
  padding-left: 0px;
}

.radius {
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -o-border-radius: 8px;
  border-radius: 8px;
}
.pointer {
  cursor: pointer;
}

/*placeholder*/
input::placeholder,
textarea::placeholder {
  color: #929398;
  opacity: 1;
  /* Firefox */
}

