.pSticky {
    position: sticky;
    width: 100%;
    top: 0px;
    max-height: 300px;
  }
  .transition {
    -webkit-transition: all 0.3s ease 0s;
    -moz-transition: all 0.3s ease 0s;
    -o-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
  }
  button[disabled=""] {
    background: #929398;
  }
  .cover{
      object-fit: cover;
  }
  .radiusTop{
    -webkit-border-radius: 7px 7px 0px 0px;
    -moz-border-radius: 7px 7px 0px 0px;
    border-radius: 7px 7px 0px 0px;
  }
 body .radiusRight{
    -webkit-border-radius: 0px 7px 7px 0px;
    -moz-border-radius: 0px 7px 7px 0px;
    border-radius: 0px 7px 7px 0px;
  }
  body .radiusLeft{
    -webkit-border-radius: 7px 0px 0px 7px;
    -moz-border-radius: 7px 0px 0px 7px;
    border-radius: 7px 0px 0px 7px;
  }
  .radius4{
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
  }
  .textDecorationNone{
      text-decoration: none;
  }
  .iconOverInput{
    position: absolute;
    top: 10px;
    left: 15px;
  }
  input{
    height: 47px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .inputSolid {
    background: var(--colorLightOrange);
    border: 2px solid transparent;
}
.inputSolid:focus {
  border-color: var(--colorOrange);
  outline: none;
}
.colorPara{
  color: var(--colorPara);
}
.bBottom {
  border-bottom: 1px solid #dee2e6;
}
.bRight {
  border-right: 1px solid #dee2e6;
}
.bTop {
  border-top: 1px solid #dee2e6;
}
.bLeft {
  border-bottom: 1px solid #dee2e6;
}
.bBnone{
  border-bottom: none;
}
.bTnone{
  border-top: none;
}
.bLnone{
  border-left: none;
}
.bRnone{
  border-right: none;
}
.outlineNone{
  outline: none !important;
}
body .radiusFull{
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -o-border-radius: 50px;
  border-radius: 50px;
}
.radius100{
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -o-border-radius: 100%;
  border-radius: 100%;
}
.textShadow{
  -webkit-text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}
.dotted_border{
  border-bottom: 2px dotted #E8E9E9;
}
.sidebarHeight {
  height: 100vh;
  overflow-y: auto;
}
.table > :not(:first-child) {
  border-top: 1px solid #e8e9e9;
}
.table > :not(caption) > * > * {
  padding: 13px 10px;
}
.modal.show .modal-dialog.megaMenuModal{
  -webkit-animation-name: stickySlideDown;
animation-name: stickySlideDown;
animation-duration: 0.3s;
animation-direction: normal;
}

@-webkit-keyframes stickySlideDown {
  0% {
    opacity: 0.7;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes stickySlideDown {
  0% {
    opacity: 0.7;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
.modal-open {
  padding-right: 0px !important;
}

.modal.fade .modal-dialog {-webkit-transform: translate(0,0)scale(.5);transform: translate(0,0)scale(.5);
-webkit-transition: all 0.2s ease-out 0s;
transition: all 0.2s ease-out 0s;
}
.modal.show .modal-dialog {-webkit-transform: translate(0,0)scale(1);transform: translate(0,0)scale(1);
  -webkit-transition: all 0.2s ease-out 0s;
  transition: all 0.2s ease-out 0s;
}